<template>
	<view>
		<!-- 导航栏组件 -->
		<navigation :userInfo="true"></navigation>
		<view class="contain">
			<!-- 内容 -->
			<view class="userInfo-con  padding">
				<view>
					<!-- 头像 -->
					<view class="flex justify-center">
						<view class="">
							<image class="portrait" :src="userInfoObj.avatar" mode="aspectFill"></image>
						</view>
						<view @click.capture="putAvatar()" class="mask">
							<text class="cuIcon-cameraaddfill color-fff"></text>
						</view>
						<view class="progress">
							<view class=""></view>
							<text>资料完成度<text class="color-lvse">100%</text></text>
						</view>

					</view>

					<view @click="putAvatar()" class="editavatar">点击更换头像</view>

					<u--form :rules="rules" labelPosition="left" :model="userInfoObj" ref="uForm">
						<u-form-item required labelWidth="150rpx" label="昵称" prop="nickname">
							<u--input v-model="userInfoObj.nickname" placeholder="请填写昵称" border="none"></u--input>
						</u-form-item>

						<u-form-item labelWidth="150rpx" label="性别" prop="gender" @click="sexShow = true">
							<u--input readonly v-model="userInfoObj.gender" placeholder="请选择性别" border="none">
							</u--input>
							<u-icon slot="right" name="arrow-right"></u-icon>
						</u-form-item>

						<u-form-item labelWidth="150rpx" label="生日" prop="birthday" @click="dateShow = true">
							<u--input readonly :value="userInfoObj.birthday | moment" placeholder="请选择生日" border="none">
							</u--input>

							<u-icon slot="right" name="arrow-right"></u-icon>
						</u-form-item>

						<u-form-item labelWidth="150rpx" label="职业" prop="profession" @click="workShow = true">
							<u--input readonly v-model="userInfoObj.profession" placeholder="请选择职业" border="none">
							</u--input>
							<u-icon slot="right" name="arrow-right"></u-icon>
						</u-form-item>
						<view class="flex justify-around padding-tb-lg margin-tb-lg">
							<button @click="confirmEdit()" class="btn2 cu-btn bg-qita color-fff lg">修改</button>
						</view>
					</u--form>
				</view>
				<!-- 选择器 -->
				<u-picker :show="sexShow" v-model="userInfoObj.gender" :immediateChange="true" :showToolbar="false"
					:columns="sexColumns" @close="sexShow = false" :closeOnClickOverlay="true" itemHeight="50"
					@change="sexChange"></u-picker>
				<u-picker :show="workShow" v-model="userInfoObj.profession" :immediateChange="true" :showToolbar="false"
					:columns="workColumns" @close="workShow = false" :closeOnClickOverlay="true" itemHeight="50"
					@change="workChange"></u-picker>
				<u-datetime-picker @change="birthdayChange" :show="dateShow" v-model="userInfoObj.birthday"
					:showToolbar="false" @close="dateShow = false" :closeOnClickOverlay="true" mode="date">
				</u-datetime-picker>

				<u-toast ref="uToast"></u-toast>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		columns1
	} from '../../utils/occupation.js'
	import moment from 'moment'
	import {
		editUserInfo
	} from '../../api/user.js'
	export default {
		data() {
			return {
				rules: {
					'nickname': [{
						required: true,
						message: '请输入姓名',
						trigger: ['blur', 'change']
					}]
				},
				// 用户信息参数
				userInfoObj: {},
				sexShow: false,
				dateShow: false,
				workShow: false,
				sexColumns: [
					['男', '女', '未知']
				],
				workColumns: columns1
			};
		},
		onLoad() {
			let channel = this.getOpenerEventChannel()
			channel.on('item', (data) => {
				console.log(data);
				this.userInfoObj = data.data
			})
		},
		methods: {
			// 选择性别
			sexChange(e) {
				this.userInfoObj.gender = e.value.toString()
			},
			//选择生日
			birthdayChange(e) {
				this.userInfoObj.birthday = moment(e.value).format('YYYY-MM-DD')
				console.log(this.userInfoObj.birthday);
			},
			// 选择职业
			workChange(e) {
				this.userInfoObj.profession = e.value.toString()
			},
			// 修改个人信息
			async confirmEdit() {
				this.$refs.uForm.validate().then(async res => {
					await editUserInfo(this.userInfoObj)
					this.$refs.uToast.show({
						message: '修改成功',
						type: 'success',
						complete: () => {
							uni.navigateBack({
								delta: 1
							})
						}
					})
				}).catch(errors => {
					uni.$u.toast('校验失败')
				})
			},
			putAvatar() {
				// 调用打开相册API
				uni.chooseImage({
					sourceType: ['album'],
					success: async (res) => {
						let q = res.tempFilePaths.toString()
						this.userInfoObj.avatar = q
						// 调用预览图片API
						uni.previewImage({
							urls: res.tempFilePaths,
							longPressActions: {
								itemList: ['分享', '保存图片', '收藏'],
								success: (data) => {
									console.log('选中了第' + (data.tapIndex + 1) + '个按钮，第' + (data
										.index + 1) + '张图片');
								},
								fail: function(err) {
									console.log(err.errMsg);
								}
							}
						})
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.contain {
		.userInfo-con {
			z-index: 99;
			height: 1100rpx;
			background-color: #151724;
			border-radius: 5% 5% 0 0;
			margin-top: -80rpx;

			.portrait {
				width: 180rpx;
				height: 180rpx;
				border-radius: 50%;
				background-color: black;
				margin-top: -80rpx;
				border: 6rpx solid black;
			}

			.mask {
				position: absolute;
				top: 292rpx;
				width: 180rpx;
				height: 180rpx;
				border-radius: 50%;
				background: rgba(0, 0, 0, 0.4);
				font-size: 60rpx;
				display: flex;

				text {
					margin: auto;
				}
			}

			.progress {
				position: absolute;
				top: 370rpx;
				right: 50rpx;
				width: 200rpx;
				height: 90rpx;
				background-color: #1c1e2b;

				view {
					width: 170rpx;
					height: 20rpx;
					// 背景渐变色
					background: linear-gradient(90deg, #f347e5, #8a7bf8);
					border-radius: 50rpx;
					margin-left: 12rpx;
					margin-top: 18rpx;
				}

				text {
					display: inline-block;
					color: #8a8e93;
					font-size: 12rpx;
					margin-left: 8rpx;
					margin-top: 5rpx;
					// 缩小文字
					transform: scale(0.89);
				}

			}
		}

		.editavatar {
			margin: 20rpx 0 50rpx 260rpx;
			color: #f8cd18;
		}

		::v-deep .u-form-item__body__left__content__label {
			color: #8a8d96;
		}

		::v-deep .uni-input-input {
			color: #fff;
			background-color: #151724;
		}

		::v-deep .u-form-item__body {
			padding: 40rpx;
			background-color: #151724;
			border: 0;
			color: #fff;
		}
	}
</style>
